<template>
  <view>
    <view class="uni-header">
      <view class="uni-group">
        <view class="uni-title"></view>
        <view class="uni-sub-title"></view>
      </view>
      <view class="uni-group">
        <input class="uni-search" type="text" v-model="query" @confirm="search" placeholder="请输入搜索内容" />
        <button class="uni-button" type="default" size="mini" @click="search">搜索</button>
        <button class="uni-button" type="default" size="mini" @click="navigateTo('./add')">新增</button>
        <button class="uni-button" type="default" size="mini" :disabled="!selectedIndexs.length" @click="delTable">批量删除</button>
        <download-excel class="hide-on-phone" :fields="exportExcel.fields" :data="exportExcelData" :type="exportExcel.type" :name="exportExcel.filename">
          <button class="uni-button" type="primary" size="mini">导出 Excel</button>
        </download-excel>
		<view class="deriveExcel" @click="deriveQuestions">
			<button class="uni-button" type="primary" size="mini">导入 题目</button>
		</view>
      </view>
    </view>
    <view class="uni-container">
      <unicloud-db ref="udb" :collection="collectionList" field="user_id,subject_type_one,subject_type_two,title,src_title,isimg,code,option,true_option,current,topic,code2,alt" :where="where" page-data="replace"
        :orderby="orderby" :getcount="true" :page-size="options.pageSize" :page-current="options.pageCurrent"
        v-slot:default="{data,pagination,loading,error,options}" :options="options" loadtime="manual" @load="onqueryload">
        <uni-table ref="table" :loading="loading" :emptyText="error.message || '没有更多数据'" border stripe type="selection" @selection-change="selectionChange">
          <uni-tr>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'subject_type_one')" sortable @sort-change="sortChange($event, 'subject_type_one')">题目大类 </uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'subject_type_two')" sortable @sort-change="sortChange($event, 'subject_type_two')">题目小类</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'title')" sortable @sort-change="sortChange($event, 'title')">题目</uni-th>
            <uni-th align="center" filter-type="range" @filter-change="filterChange($event, 'code')" sortable @sort-change="sortChange($event, 'code')">题型</uni-th>
            <uni-th align="center" sortable @sort-change="sortChange($event, 'true_option')">正确选项</uni-th>
            <uni-th align="center" filter-type="search" @filter-change="filterChange($event, 'alt')" sortable @sort-change="sortChange($event, 'alt')">本题答案描述</uni-th>
            <uni-th align="center">操作</uni-th>
          </uni-tr>
          <uni-tr v-for="(item,index) in data" :key="index">
            <uni-td align="center">{{item.subject_type_one}}</uni-td>
            <uni-td align="center">{{item.subject_type_two}}</uni-td>
            <uni-td align="center">
				<view v-if="!item.src_title" >
					{{item.title}}
				</view>
				<view v-else >
					<uni-file-picker v-if="item.src_title && item.src_title.fileType == 'image'" :value="item.src_title" :file-mediatype="item.src_title && item.src_title.fileType" return-type="object" :imageStyles="imageStyles" readonly></uni-file-picker>
					<uni-link v-else :href="item.src_title && item.src_title.url" :text="item.src_title && item.src_title.url"></uni-link>
				</view>
			</uni-td>
			
            <uni-td align="center">{{item.code == 1 ? "单选题" : "多选题"}}</uni-td>
            <uni-td align="center">{{item.true_option}}</uni-td>
            <uni-td align="center">{{item.alt}}</uni-td>
            <uni-td align="center">
              <view class="uni-group">
                <button @click="navigateTo('./edit?id='+item._id, false)" class="uni-button" size="mini" type="primary">修改</button>
                <button @click="confirmDelete(item._id)" class="uni-button" size="mini" type="warn">删除</button>
              </view>
            </uni-td>
          </uni-tr>
        </uni-table>
        <view class="uni-pagination-box">
          <uni-pagination show-icon :page-size="pagination.size" v-model="pagination.current" :total="pagination.count" @change="onPageChanged" />
        </view>
      </unicloud-db>
    </view>
  </view>
</template>

<script>
  import { enumConverter, filterToWhere } from '../../js_sdk/validator/question_bank.js';
  import XLSX from '@/common/js/xlsx.mini.js'

  const db = uniCloud.database()
  // 表查询配置
  const dbOrderBy = '' // 排序字段
  const dbSearchFields = [] // 模糊搜索字段，支持模糊搜索的字段列表。联表查询格式: 主表字段名.副表字段名，例如用户表关联角色表 role.role_name
  // 分页配置
  const pageSize = 20
  const pageCurrent = 1

  const orderByMapping = {
    "ascending": "asc",
    "descending": "desc"
  }

  export default {
    data() {
      return {
        collectionList: "question_bank",
        query: '',
        where: '',
        orderby: dbOrderBy,
        orderByFieldName: "",
        selectedIndexs: [],
        options: {
          pageSize,
          pageCurrent,
          filterData: {},
          ...enumConverter
        },
        imageStyles: {
          width: 64,
          height: 64
        },
        exportExcel: {
          "filename": "question_bank.xls",
          "type": "xls",
          "fields": {
            "user_id": "user_id",
            "题目大类型": "subject_type_one",
            "题目小类型": "subject_type_two",
            "题目": "title",
            "题目类型": "isimg",
            "题型": "code",
            "选项": "option",
            "正确选项": "true_option",
            "当前所选": "current",
            "所选是否正确 默认为'' true为正确 控制选项高亮": "topic",
            "选项选择状态 默认为true false为选择": "code2",
            "本题描述": "alt"
          }
        },
        exportExcelData: []
      }
    },
    onLoad() {
      this._filter = {}
    },
    onReady() {
      this.$refs.udb.loadData()
    },
    methods: {
		//导入excel的功能函数
		deriveQuestions() {
			uni.chooseFile({
				count: 1,
				extension: ['.xls', '.xlsx'],
				success: res => {
					console.log('1');
					let reader = new FileReader();
					reader.onload = e => {
						const data = e.target.result
						this.process(data) // data为binary数据
					};
					reader.readAsBinaryString(res.tempFiles[0]);
				}
			})	
		},
		//解析excel的功能函数
		
		process(data){
			uni.showLoading({
				title:"上传中......"
			})
			let workbook = XLSX.read(data, {
				type: "binary"
			});
			const sheetName = workbook.SheetNames[0]
			let sheet = workbook.Sheets[sheetName]
			const options = {
				raw: false // 如果不加raw:true的话日期会被读成数字，根据开发需求决定
			}
			const rawData = XLSX.utils.sheet_to_json(sheet, options);
			console.log(rawData);
			rawData.forEach(item => {
				console.log(item);
				console.log(item.true_option);
				let arr = item.true_option.split(",")

				
				db.collection("question_bank").add({
					subject_type_one:item.subject_type_one,
					subject_type_two:item.subject_type_two,
					title:item.title,
					isimg:item.isimg,
					code:item.code,
					alt:item.alt,
					true_option:arr
				}).then(res => {
					console.log(res);
					uni.hideLoading()
					uni.showToast({
						title:"上传成功！"
					})
					
				})
			})
			// do something
		},
      onqueryload(data) {
        this.exportExcelData = data
      },
      getWhere() {
        const query = this.query.trim()
        if (!query) {
          return ''
        }
        const queryRe = new RegExp(query, 'i')
        return dbSearchFields.map(name => queryRe + '.test(' + name + ')').join(' || ')
      },
      search() {
        const newWhere = this.getWhere()
        this.where = newWhere
        this.$nextTick(() => {
          this.loadData()
        })
      },
      loadData(clear = true) {
        this.$refs.udb.loadData({
          clear
        })
      },
      onPageChanged(e) {
        this.selectedIndexs.length = 0
        this.$refs.table.clearSelection()
        this.$refs.udb.loadData({
          current: e.current
        })
      },
      navigateTo(url, clear) {
        // clear 表示刷新列表时是否清除页码，true 表示刷新并回到列表第 1 页，默认为 true
        uni.navigateTo({
          url,
          events: {
            refreshData: () => {
              this.loadData(clear)
            }
          }
        })
      },
      // 多选处理
      selectedItems() {
        var dataList = this.$refs.udb.dataList
        return this.selectedIndexs.map(i => dataList[i]._id)
      },
      // 批量删除
      delTable() {
        this.$refs.udb.remove(this.selectedItems(), {
          success:(res) => {
            this.$refs.table.clearSelection()
          }
        })
      },
      // 多选
      selectionChange(e) {
        this.selectedIndexs = e.detail.index
      },
      confirmDelete(id) {
        this.$refs.udb.remove(id, {
          success:(res) => {
            this.$refs.table.clearSelection()
          }
        })
      },
      sortChange(e, name) {
        this.orderByFieldName = name;
        if (e.order) {
          this.orderby = name + ' ' + orderByMapping[e.order]
        } else {
          this.orderby = ''
        }
        this.$refs.table.clearSelection()
        this.$nextTick(() => {
          this.$refs.udb.loadData()
        })
      },
      filterChange(e, name) {
        this._filter[name] = {
          type: e.filterType,
          value: e.filter
        }
        let newWhere = filterToWhere(this._filter, db.command)
        if (Object.keys(newWhere).length) {
          this.where = newWhere
        } else {
          this.where = ''
        }
        this.$nextTick(() => {
          this.$refs.udb.loadData()
        })
      }
    }
  }
</script>

